<template>
    <div class="c_deploy">
        <!-- <el-dialog title="权限管理" :visible.sync="visible" @colse="$emit('update:show',false)" :show="show" width="690px"> -->
        <el-dialog :title="title" :visible.sync="visible" @close="$emit('update:dialogShow', false)" :dialogShow="dialogShow" width="690px">
            <!-- 分页内容区域 -->
            <div class="c_dialog">
                <div class="c_dialog_left">
                    <div class="c_dialog_left_top">
                        <!-- <el-input class="c_dialog_left_input" placeholder="请输入内容">
                            <i slot="prefix" class="el-input__icon el-icon-search"></i>
                        </el-input> -->
                        <div class="c_dialog_input">
                            <img class="c_dialog_icon" :src="require('../assets/img/power/search.png')" alt>
                            <el-input placeholder="输入关键字"></el-input>
                        </div>
                        <el-button class="c_dialog_left_btn" icon="el-icon-plus"></el-button>
                    </div>
                    <!-- 树形菜单区域 -->
                    <div class="c_tree_menu">
                        <!-- 接收的： menuList  写死的：  corpList :corpData="menuList" -->
                        <!-- <DepTree @item="clickDep" @loadMore="clickMore"  /> -->
                        <DepTree :isShowIcon="false" :searchType="searchType" :corpData="showData" />
                    </div>
                </div>
                <div class="c_dialog_rigth">
                    <div class="c_dialog_rigth_title">{{twoTitle}}</div>
                    <div v-for="(item,index) in powerList" :key="item.id" class="c_list">
                        <div class="c_list_text">
                            <!-- <img class="c_list_text_icon" :src="require('../assets/img/power/dep.png')" alt=""> -->
                            {{item.name}}
                        </div>
                        <!-- <div class="c_list_icon">
                            <i class="el-icon-delete"></i>
                        </div> -->
                        <el-button @click="delPowerList(index)" class="c_list_icon" type="text">
                            <i class="el-icon-delete"></i>
                        </el-button>
                    </div>
                </div>
            </div>
            <!-- 底部 -->
            <span slot="footer" class="dialog-footer">
                <el-button class="c_footerbtn" type="primary" @click="submitBtn">提交</el-button>
                <el-button class="c_footerbtn" @click="resetBtn">重置</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    function recursiveChangeItem(changeItem, findKey) {
        console.log('lalalal');
        
        console.log(changeItem);
        for (let i = 0; i < changeItem.length; i++) {
            
            
            if (changeItem[i].children.length > 0) {
                recursiveChangeItem(changeItem[i].children, findKey);
            } else {
                let isColor = false
                for (let j = 0; j < findKey.length; j++) {
                    if (changeItem[i].depid === findKey[j].depid) {
                        isColor = true;
                        break;
                    }
                }
                changeItem[i].isColor = isColor

            }
            
        }
        return changeItem

    }
    import DepTree from "./c_departmentsList.vue";
    export default {
        data() {
            return {

                visible: this.dialogShow,
                // 部门数据
                corpList: [{}], // 顶级菜单列表
                corpDepList: [{}], //子级菜单列表
                searchType: 'all',
                allData: [],
                isAddPowerList: false,
                // childrenList: []
                //powerList: [] //已选权限
                // setPowerList: JSON.parse(JSON.stringify(this.powerList))
            };
        },
        props: { //接收父组件传值

            dialogShow: { //是否弹框
                type: Boolean,
                default: false
            },
            powerList: { //已选权限
                type: Array
            },
            title: {
                type: String,
                default: '权限管理'
            },
            twoTitle: {
                type: String,
                default: '用户将拥有以下权限'
            },
        },

        watch: {
            dialogShow() {
                this.visible = this.dialogShow;
            }
        },
        methods: {
            delPowerList(index) { //删除已选权限
                // this.$emit('delete',index)
                this.powerList.splice(index, 1)
                // recursiveChangeItem(this.corpList,this.powerList)
                // console.log(this.corpList);
                
            },
            /**
             * 点击返回按钮给父组件传值
             */
            submitBtn() {
                // console.log('1111');

                this.$emit('submit', this.powerList);

            },
            resetBtn() { //重置按钮
                this.$emit('reset');
            },
            //----------------------------------------------------------------------    
            // 获取用户公司
            async getCompony() {
                let result = await this.$api.addressBook.getCompony();
                // console.log(result);
                if (result.statusCode === 75200) {
                    // this.$zero.ToastSuccess("获取成功");
                    let corpInfo = result.list[0];
                    // this.corpList[0].depid = corpInfo.depid;
                    // this.corpList[0].name = corpInfo.name;
                    // 向 data 中添加数据
                    this.$set(this.corpList[0], "isCorp", true);
                    this.$set(this.corpList[0], "depid", corpInfo.depid);
                    this.$set(this.corpList[0], "name", corpInfo.name);
                    this.$set(this.corpList[0], "show", false);
                    this.$set(this.corpList[0], "children", []);

                    // this.company_id = corpInfo.depid;
                    // 获取完成之后自动的自己去加载首页部门
                    this.depLoadMore({
                        depid: corpInfo.depid
                    });
                } else if (result.statusCode === 72305) {
                    this.$zero.ToastWarning("用户或分组不存在");
                } else if (result.statusCode === 72315) {
                    this.$zero.ToastWarning("请核对公司id");
                } else if (result.statusCode === 75500) {
                    this.$zero.ToastWarning("服务异常，请稍后再试");
                }
            },

            /**
             * 点击 部门 或者 + 号 去加载更多
             *  在此处判断是 加载部门 还是加载所有
             **/
            async depLoadMore(item, searchType = "all") {
                // console.log('我再那、、、、222222');
                // console.log(item);

                // console.log("请求的是加载所有的");
                // 请求所有的人和部门
                const itemDepId = item.depid;
                if (!itemDepId) {
                    return this.$zero.ToastError("无效的部门id");
                }
                // 根据获取到的部门 id 拿取信息数据
                // let result = await this.$api.addressBook.getDepAllMemeber({
                //     depid: itemDepId
                // });
                let result = [];

                if (item.level !== undefined) {
                    result = await this.getSublevelMenu(item)
                } else {
                    result = await this.getTopMenu(itemDepId)
                }

                console.log('我再那、、、、');
                // console.log("获取到的部门信息数据");
                // console.log(result);
                if (result.statusCode === 72315) {
                    return this.$zero.ToastError("请核对公司id");
                } else if (result.statusCode === 75500) {
                    return this.$zero.ToastError("服务异常，请稍后再试");
                } else if (result.statusCode === 75200) {
                    var children = [];
                    for (let i = 0; i < result.menus.length; i++) {
                        children[i] = {
                            name: result.menus[i]._name,
                            id: result.menus[i]._id,
                            level: result.menus[i].level,
                            parent_id: result.menus[i].parent_id,
                            serial: result.menus[i].serial,
                            switch: result.menus[i].switch,
                            type: "dep",
                            depid: result.menus[i]._id,
                            company_id: itemDepId,
                            children: []
                        }
                    }
                    // console.log('lololo');
                    // console.log(this.powerList);
                    if(this.isAddPowerList){
                        for (let i = 0; i < children.length; i++) {
                            let isok = true
                            for (let j = 0; j < this.powerList.length; j++) {
                                if(this.powerList[j].depid === children[i].depid){
                                    isok = false;
                                    break;
                                }
                                
                            }
                            if(isok){
                                this.powerList.push(children[i]);
                            }
                        }
                        this.isAddPowerList = false
                    }
                    

                    // this.childrenList = children;
                    


                    let isAdd = true;
                    if (this.allData.length > 0 && children.length > 0) {
                        for (let i = 0; i < this.allData.length; i++) {
                            if (this.allData[i].depid === children[0].depid) {
                                isAdd = false;
                                break;
                            }

                        }
                    }
                    if (isAdd) {
                        this.allData = this.allData.concat(children);
                    }
                    console.log(this.allData);



                    function recursiveFindItem(obj) {
                        for (var key in obj) {
                            if (typeof obj[key] === "object") {
                                recursiveFindItem(obj[key]);
                            } else {
                                if (obj[key] === itemDepId) {
                                    obj.show = !obj.show;
                                    return (obj.children = children);
                                }
                            }
                        }
                    }
                    // 递归循环将数据改变
                    recursiveFindItem(this.corpList, itemDepId);
                    // if(this.powerList.length > 0){
                    //     recursiveChangeItem(this.corpList,this.powerList)
                    //     console.log('this.corpList');
                        
                    //     console.log(this.corpList);
                        
                    // }
                }


            },


            //获取顶级菜单
            async getTopMenu(itemDepId) {
                let result = await this.$api.authorized.getTopMenu({
                    company_id: 'cp_001' || itemDepId
                });

                return result
            },
            // 获取子级
            async getSublevelMenu(obj) {
                console.log(obj);

                let result = await this.$api.authorized.getSublevelMenu({
                    company_id: obj.company_id,
                    menuid: obj.id
                });

                return result
            }
        },
        computed: {
            showData() { //返回那个级别的数据
                return this.corpList;
            }
        },
        created() {
            this.getCompony();
            // this.depLoadMore()


        },
        // updated(){
        //     this.getPowerList();
        // },
        mounted() {
            this.$zero.eventBus.$off("departmentItem").$on("departmentItem", obj => {
                let {
                    item,
                    searchType
                } = obj;

                for (let i = 0; i < this.powerList.length; i++) {
                    if (this.powerList[i].id === item.id) {
                        this.$zero.ToastError("已选择");
                        return
                    }
                }
                this.isAddPowerList = true; 

                let childrenList = [];

                function clickUserAddParentDep(arr, parent_id) {            
                    for (let i = 0; i < arr.length; i++) {
                        if (arr[i].depid === parent_id) {
                            childrenList.unshift(arr[i])
                            clickUserAddParentDep(arr, arr[i].parent_id)
                            break;
                        }

                    }
                }
                clickUserAddParentDep(this.allData, item.parent_id)

                let list = JSON.parse(JSON.stringify(this.powerList))
                for (let i = 0; i < childrenList.length; i++) {
                    let isok = true
                    for (let j = 0; j < list.length; j++) {
                        if (childrenList[i].depid === list[j].depid) {
                            isok = false
                            break;
                        }
                    }
                    if (isok) {
                        this.powerList.push(childrenList[i])
                    }

                }

                this.powerList.push(item);

                if (this.powerList.length < 2) {
                    return;
                }
                for (let j = 0; j < this.powerList.length; j++) {
                    for (let i = 1; i < this.powerList.length; i++) {
                        if (this.powerList[i].serial < this.powerList[i - 1].serial) {
                            let temp = this.powerList[i];
                            this.powerList[i] = this.powerList[i - 1]
                            this.powerList[i - 1] = temp
                        }

                    }

                }
                // console.log(this.powerList);




                // this.depLoadMore(item, searchType);
            });

            this.$zero.eventBus.$off("addBookLoadMore").$on("addBookLoadMore", obj => {
                console.log('我竟来了。。。。。。。。。。。。。。');
                let {
                    item,
                    searchType
                } = obj;
                console.log(item, searchType);

                this.depLoadMore(item, searchType);
            });

        },
        components: {
            DepTree
        }
    };
</script>

<style scoped>
    .c_deploy .el-dialog__header {
        border-bottom: 1px solid #000;
    }

    .c_deploy .el-dialog__body {
        padding-top: 15px;
        padding-bottom: 55px;
    }

    /* 弹框内容区域 */
    .c_dialog {
        width: 100%;
        height: 520px;
        display: flex;
        justify-content: space-between;
    }

    .c_dialog_left {
        width: 385px;
        height: 520px;
        overflow: hidden;
    }

    .c_dialog_left_top {
        display: flex;
        margin-bottom: 12px;

    }

    .c_dialog_input {
        flex-grow: 1;
        position: relative;
    }

    .c_dialog_icon {
        position: absolute;
        left: 15px;
        top: 12px;
        z-index: 9;
        width: 16px;
        height: 16px;
    }


    .c_dialog_left_btn {
        padding: 6px;
        margin-left: 10px;
        border-color: #2f78ef;
        color: #2f78ef;
        font-size: 24px;
    }

    /* 树形菜单区域 */
    .c_tree_menu {
        width: 100%;
        height: 90%;
        background-color: #f2f2f2;
        overflow: auto;
    }

    /* 分页右侧区域 */
    .c_dialog_rigth {
        width: 245px;
        height: 520px;
        border-left: 1px solid #e6e6e6;
        padding-left: 20px;
        box-sizing: border-box;
        overflow: auto;
    }

    .c_dialog_rigth_title {
        font-size: 14px;
        color: #333;
        font-weight: bold;
        margin-bottom: 5px;
    }

    .c_list {
        display: flex;
        justify-content: space-between;
        padding-top: 10px;
        font-size: 14px;
    }

    .c_list_text {
        color: #4d4d4d;
        display: flex;
        align-items: center;
    }

    .c_list_text_icon {
        width: 14px;
        height: 14px;
        margin-right: 5px;
    }


    .c_list_icon {
        padding: 0;
        color: #59514f;
    }

    /* 分页底部 */
    .c_footerbtn {
        padding: 12px 30px;
        border-radius: 0px;
        font-weight: bold;
    }


    /* .c_deploy  .el-dialog__header {
        border-bottom: 1px solid #d9d9d9;
        font-weight: bold;
    }

    .c_deploy  .el-dialog__body {
        padding-top: 15px;
        padding-bottom: 55px;
    }

    .c_deploy  .el-dialog__footer {
        border-top: 1px solid #d9d9d9;
        padding: 15px 20px;
    }

    .c_deploy .c_dialog_input .el-input__inner {
        text-indent: 18px;
        border: 1px solid #e1e1e1;
    } */
</style>