<template>
  <!-- 
  此组件不要引入，通讯录模块引入 c_departmentTree 组件，c_departmentTree引入此文件
  -->
  <div class="c_department_list">
    <div class="c_department_list_item" v-for="item in corpData" :key="item.depid">
      <div class="c_department_list_item_row" @mouseleave="mouseLeave(item)">
        <!-- 前方展示图标 -->
        <div class="cdl_icon_img" v-if="isShowIcon || item.isCorp">
          <img v-if="item.isCorp" :src="require('../assets/img/addressBook/corp.png')" />
          <img v-else-if="item.type === 'dep'" :src="require('../assets/img/addressBook/dep.png')" />
          <img
            v-else-if="item.type === 'user'"
            :src="require('../assets/img/addressBook/person.png')"
          />
        </div>

        <p class="c_department_list_item_name c_department_list_item_has" @click="changeItem(item)">
          <span :class="[item.isColor ? 'active' : '']" @click="emitParent(item)">{{item.name}}</span>
          <i
            v-if="item.type !== 'user'"
            class="edl_add_icon"
            :class="{edl_add_icon_close:item.show}"
          ></i>
        </p>

        <div class="c_department_list_item_elli" v-if="isUseSpot">
          <img :src="require('../assets/img/addressBook/more.png')" alt @click="showUpdate(item)" />
          <div class="c_department_list_item_elli_con" v-if="item.isShowUpdate">
            <div class="c_department_list_item_elli_con_list">
              <div class="c_department_list_item_elli_con_item" @click="addChildDep(item)">添加子部门</div>
              <div class="c_department_list_item_elli_con_item" @click="changeDepName(item)">修改名称</div>
              <div class="c_department_list_item_elli_con_item" @click="deleteDep(item)">删除</div>
              <div class="c_department_list_item_elli_con_item">部门id {{item.depid}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 判断有没有children 和 是否显示 递归全部渲染出来 -->
      <DepTree
        v-if="item.children && item.show"
        :isShowIcon="isShowIcon"
        :searchType="searchType"
        :corpData="item.children"
        :corpTreeId="corpTreeId"
        :isUseSpot="isUseSpot"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "DepTree",
  props: {
    corpData: {
      type: Array
    },
    searchType: {
      type: String,
      default: "all"
    },
    isShowIcon: {
      type: Boolean,
      default: true
    },
    // 是否使用右侧三个点 默认不启用
    isUseSpot: {
      type: Boolean,
      default: false
    },

    // 使用不同的id去标识不同的集团树 要给每一个树一个不同的 corpTreeId
    corpTreeId: {
      type: String,
      default: "1"
    }
  },
  methods: {
    // 点击文字 和 + 都要去请求
    changeItem(item) {
      // 这个事件传送给事件总线
      this.$zero.eventBus.$emit("addBookLoadMore", {
        item,
        searchType: this.searchType,
        corpTreeId: this.corpTreeId
      });
    },

    // 点击文字 传值
    emitParent(item) {
      // console.log('触发了此函数');
      /**
       * 传输数据内容
       *  item {Object} 点击的列表项的值
       *  this.searchType 类型
       */
      this.$zero.eventBus.$emit("departmentItem", {
        item,
        searchType: this.searchType,
        corpTreeId: this.corpTreeId
      });
    },

    /**
     * 点击右侧三个点 选择
     */
    clickSpot(item) {
      console.log("点击了我");
      console.log(item);
      // 传参数
      this.$$zero.eventBus.$emit("clickSpot", {
        item,
        corpTreeId: this.corpTreeId,
        searchType: this.searchType
      });
    },

    // 鼠标移开
    mouseLeave(item) {
      // console.log('鼠标移开的项为');
      // console.log(item);
      if (!this.isUseSpot) {
        return;
      }
      item.isShowUpdate = false;
    },

    // 点击显示展开右侧三个点的内容
    showUpdate(item) {
      // console.log('展开了');
      // console.log(item);
      item.isShowUpdate = true;
    },

    // 下面三个事件只有在通讯录模块有三个点 corpTreeId = ad10 的时候触发
    // 其他页面需要加入请传入 或者 corpTreeId === 你定义的 corpTreeId
    // 添加子部门
    addChildDep(item) {
      console.log("点击了添加子部门");
      // console.log(item);
      if (this.corpTreeId === "ad10") {
        // 冒泡传递给通讯录页面
        this.$zero.eventBus.$emit("addChildDep", {
          item,
          corpTreeId: this.corpTreeId,
          searchType: this.searchType
        });
      }
    },

    // 修改子部门名称
    changeDepName(item) {
      console.log("修改子部门名称");
      if (this.corpTreeId === "ad10") {
        // 冒泡传递给通讯录页面
        this.$zero.eventBus.$emit("changeDepName", {
          item,
          corpTreeId: this.corpTreeId,
          searchType: this.searchType
        });
      }
    },

    // 删除子部门
    deleteDep(item) {
      console.log("删除部门");
      if (this.corpTreeId === "ad10") {
        // 冒泡传递给通讯录页面
        this.$zero.eventBus.$emit("deleteDep", {
          item,
          corpTreeId: this.corpTreeId,
          searchType: this.searchType
        });
      }
    }
  },

  mounted() {
    // console.log('daasd')
    // console.log(this.isShowIcon)
    // console.log(this.searchType);
    // console.log(this.corpData.isCorp)
    // console.log(this.corpData)
  }
};
</script>
<style scoped>
.c_department_list {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0 0 0 25px;
}
.cdl_icon_img img {
  width: 14px;
  height: 14px;
}
.c_department_list_item_row {
  position: relative;
  display: flex;
  height: 25px;
  padding: 3px 0;
  align-items: center;
}

.c_department_list_item_row:hover {
  background: rgba(47, 120, 239, 0.1);
}
.c_department_list_item_children {
  padding-left: 25px;
  overflow: hidden;
}
.c_department_list_item_name {
  position: relative;
  margin-left: 8px;
  cursor: pointer;
}
.edl_add_icon {
  position: absolute;
  right: -16px;
  top: 5px;
  width: 11px;
  height: 11px;
  background: red;
  cursor: pointer;
  background: transparent url("../assets/img/addressBook/open.png") no-repeat;
}
.edl_add_icon_close {
  background: transparent url("../assets/img/addressBook/retract.png") no-repeat;
}
.active{
    color: blue;
}

/* 右侧三个点部分*/
.c_department_list_item_row:hover .c_department_list_item_elli {
  display: block;
}

.c_department_list_item_elli {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 100%;
  font-size: 14px;
}
.c_department_list_item_elli img {
  width: 20px;
  height: 20px;
  padding-top: 5px;
}
.c_department_list_item_elli_con {
  position: absolute;
  top: 0px;
  left: 20px;
  width: 120px;
  line-height: 35px;
  background: transparent;
  text-indent: 17px;
  padding-top: 15px;
}
.c_department_list_item_elli_con_list{
  background: #fff;
}
.c_department_list_item_elli_con_item {
  background: #fff;
}
.c_department_list_item_elli_con_item:hover {
  background: rgba(47, 120, 239, 0.1);
  cursor: pointer;
}
.c_department_list_item_elli_con_item:last-child {
  color: #aaa;
}
.c_department_list_item_elli_con_item:last-child:hover {
  cursor: default;
  background: #fff;
}
/* 右侧三个点部分 */
</style>
